<template>
  <div>
    <div style="display: flex;justify-content: center;margin-top: 150px">
      <el-card style="width: 400px">
        <div slot="header" class="clearfix">
          <span></span>
        </div>
        <table>
          <tr>
            <td></td>
            <td>
              <el-input type="tel" v-model="user.phonenumber" placeholder=""></el-input>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <el-input v-model="user.username" placeholder=""></el-input>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <el-input v-model="user.email" placeholder=""></el-input>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <el-input type="password" v-model="user.password1" placeholder="" ></el-input>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <el-input type="password" v-model="user.password2" placeholder="" @keydown.enter.native="doLogin"></el-input>
            </td>
          </tr>
          <tr>
            <!-- -->
            <td colspan="2">
              <el-button style="width: 350px" type="primary" @click="doRegister"></el-button>
            </td>
          </tr>
        </table>
      </el-card>
    </div>
  </div>
</template>

<script>
import {postNewUser} from '../../api/api.js'
export default {
  data () {
    return {
      user: {
        phonenumber: '',
        username: '',
        password1: '',
        password2: '',
        email: ''
      }
    }
  },
  methods: {
    doRegister () {
      postNewUser(this.user.username, this.user.password1, this.user.password2, this.user.phonenumber, this.user.email)
        .then(response => {
          console.log(response)
          alert('')
          this.$router.push({ path: '/Login' })
        }).catch(error => {
          console.log(error)
          alert(' ')
        })
    }
  }
}
</script>



